@import "../../global.scss";
.portfolio{
    background-color: #ffc482;
    display: flex;
    flex-direction: column;
    align-items: center;

    h1{
        margin-top: 20px;
        font-size: 50px;
        @include mobile {
            font-size: 20px ;
            padding-bottom: 15px;
            overflow: hidden;
        }
        
    }

    ul{
        margin: 10px;
        font-size: 30px;
        padding: 0px ;
        list-style: none;
        display: flex;
      
       
        @include mobile {
            font-size: 14px;
            margin: 10px 0;
            flex-wrap: wrap;
            justify-content: center;
            overflow: hidden;
        }
       
    }
    .container{
        width: 70%; 
        display: flex;
         align-items: center;
         justify-content: center;
         flex-wrap: wrap;

         @include mobile {
            width: 100%;
        }

         .item{
             width: 230px;
             height: 150px;
             border-radius: 20px;
             border: 1px solid rgb(146, 145, 145);
             margin: 10px 20px;
             display: flex;
             align-items: center;
             justify-content: center;
             position: relative;
             color: white;
             transition: all 1s ease;
             cursor: pointer;


             @include mobile {
                width: 130px;
                height: 10 0px;
                overflow: hidden;
            }
             h3{
                 position: absolute;
                 font-size: 20px;
             }
             img{
                 width: 100%;
                 height: 100%;
                 object-fit: cover;
                 z-index: 1;
             }
             &:hover{
                 background-color: $mainColor;

                 img{
                     opacity: 0.2;
                     z-index: 0;
                 }
             }
         }

    }
}